import { Component, OnInit } from '@angular/core';
import { ContentService } from '../core/content.service';
import { Farm } from '../core/farm';

declare interface TableData {
  headerRow: string[];
}

@Component({
  selector: 'app-total-farms',
  templateUrl: './total-farms.component.html',
  styleUrls: ['./total-farms.component.css']
})
export class TotalFarmsComponent implements OnInit {

  public tableData: TableData;

  farmsList: Farm[];
  constructor(
    private contentService: ContentService
  ) { }

  ngOnInit() {
    this.tableData = {
      headerRow: ['ID', 'Farmer ID', 'Title', 'Region', 'Size', 'Soil', 'Description']
    };
    this.contentService.getFarmsData().snapshotChanges().subscribe(item => {
      this.farmsList = [];
      item.forEach(element => {
        const y = element.payload.toJSON();
        y['fid'] = element.key;
        this.farmsList.push(y as Farm);
      });
    });
  }
}
