import { Component, OnInit, ElementRef } from '@angular/core';
import { ADMINISTRATOR_ROUTES } from '../sidebar/sidebar.component';
import { FARMER_ROUTES } from '../sidebar/sidebar.component';
import { CONSULTANT_ROUTES } from '../sidebar/sidebar.component';
import { Location } from '@angular/common';
import { Router } from '@angular/router';
import { AuthService } from '../../core/auth.service';
import { AngularFireDatabase } from '@angular/fire/database';
import { AngularFireObject } from '@angular/fire/database';
import { User } from '../../core/user';
import { ContentService } from '../../core/content.service';
import { useAnimation } from '@angular/animations';

@Component({
    selector: 'app-navbar',
    templateUrl: './navbar.component.html',
    styleUrls: ['./navbar.component.css']
})
export class NavbarComponent implements OnInit {
    private listTitles: any[];
    public location: Location;
    public mobile_menu_visible: any = 0;
    private toggleButton: any;
    private sidebarVisible: boolean;
    public isCollapsed = true;
    private user: User = new User;
    private userReference: AngularFireObject<any>;
    public userID;
    public yearList = ['2019', '2018', '2017', '2016', '2015', '2014', '2013', '2012', '2011', '2010', '2009'];

    constructor(
        private afDb: AngularFireDatabase,
        public authService: AuthService,
        private contentService: ContentService,
        location: Location,
        private element: ElementRef,
        private router: Router) {
        this.location = location;
        this.sidebarVisible = false;
        this.contentService.getFarmStatistics();
        this.contentService.getIncomeStatistics();
        this.userID = this.authService.getCurrentUserId();
    }

    refreshStatistics() {
        this.contentService.refreshFarmStats();
        this.contentService.refreshProductionStats();
        this.contentService.refreshOutcomeStats();
        this.contentService.refreshIncomeStats();
    }

    changeYear(year) {
        if (this.user.role !== 'farmer') {
            this.contentService.tempYear = year;
            this.contentService.refreshFarmStats();
            this.contentService.refreshProductionStats();
            this.contentService.refreshOutcomeStats();
            this.contentService.refreshIncomeStats();
        } else {
            this.contentService.callComponentMethod();
        }
    }

    ngOnInit() {
        this.retriveData();
        const navbar: HTMLElement = this.element.nativeElement;
        this.toggleButton = navbar.getElementsByClassName('navbar-toggler')[0];
        this.router.events.subscribe((event) => {
            this.sidebarClose();
            const $layer: any = document.getElementsByClassName('close-layer')[0];
            if ($layer) {
                $layer.remove();
                this.mobile_menu_visible = 0;
            }
        });
    }

    retriveData() {
        this.userReference = this.afDb.object('users/' + this.userID);
        this.userReference.snapshotChanges().subscribe(action => {
            this.user = action.payload.val() as User;
            if (this.user.role === 'administrator') {
                this.listTitles = ADMINISTRATOR_ROUTES.filter(listTitle => listTitle);
            }
            if (this.user.role === 'farmer') {
                this.listTitles = FARMER_ROUTES.filter(listTitle => listTitle);
            }
            if (this.user.role === 'consultant') {
                this.listTitles = CONSULTANT_ROUTES.filter(listTitle => listTitle);
            }
        });
    }

    collapse() {
        this.isCollapsed = !this.isCollapsed;
        const navbar = document.getElementsByTagName('nav')[0];
        console.log(navbar);
        if (!this.isCollapsed) {
            navbar.classList.remove('navbar-transparent');
            navbar.classList.add('bg-white');
        } else {
            navbar.classList.add('navbar-transparent');
            navbar.classList.remove('bg-white');
        }
    }

    sidebarOpen() {
        const toggleButton = this.toggleButton;
        const mainPanel = <HTMLElement>document.getElementsByClassName('main-panel')[0];
        const html = document.getElementsByTagName('html')[0];
        if (window.innerWidth < 991) {
            mainPanel.style.position = 'fixed';
        }

        setTimeout(function () {
            toggleButton.classList.add('toggled');
        }, 500);

        html.classList.add('nav-open');
        this.sidebarVisible = true;
    }

    sidebarClose() {
        const html = document.getElementsByTagName('html')[0];
        this.toggleButton.classList.remove('toggled');
        const mainPanel = <HTMLElement>document.getElementsByClassName('main-panel')[0];

        if (window.innerWidth < 991) {
            setTimeout(function () {
                mainPanel.style.position = '';
            }, 500);
        }
        this.sidebarVisible = false;
        html.classList.remove('nav-open');
    }

    sidebarToggle() {
        // const toggleButton = this.toggleButton;
        // const html = document.getElementsByTagName('html')[0];
        const $toggle = document.getElementsByClassName('navbar-toggler')[0];

        if (this.sidebarVisible === false) {
            this.sidebarOpen();
        } else {
            this.sidebarClose();
        }
        const html = document.getElementsByTagName('html')[0];

        if (this.mobile_menu_visible === 1) {
            // $('html').removeClass('nav-open');
            html.classList.remove('nav-open');
            if ($layer) {
                $layer.remove();
            }
            setTimeout(function () {
                $toggle.classList.remove('toggled');
            }, 400);

            this.mobile_menu_visible = 0;
        } else {
            setTimeout(function () {
                $toggle.classList.add('toggled');
            }, 430);

            var $layer = document.createElement('div');
            $layer.setAttribute('class', 'close-layer');


            if (html.querySelectorAll('.main-panel')) {
                document.getElementsByClassName('main-panel')[0].appendChild($layer);
            } else if (html.classList.contains('off-canvas-sidebar')) {
                document.getElementsByClassName('wrapper-full-page')[0].appendChild($layer);
            }

            setTimeout(function () {
                $layer.classList.add('visible');
            }, 100);

            $layer.onclick = function () {
                html.classList.remove('nav-open');
                this.mobile_menu_visible = 0;
                $layer.classList.remove('visible');
                setTimeout(function () {
                    $layer.remove();
                    $toggle.classList.remove('toggled');
                }, 400);
            }.bind(this);

            html.classList.add('nav-open');
            this.mobile_menu_visible = 1;

        }
    }

    getTitle() {
        let titlee = this.location.prepareExternalUrl(this.location.path());
        if (titlee.charAt(0) === '#') {
            titlee = titlee.slice(2);
        }
        titlee = titlee.split('/').pop();
        for (let item = 0; item < this.listTitles.length; item++) {
            if (this.listTitles[item].path === titlee) {
                return this.listTitles[item].title;
            }
        }
        return 'Dashboard';
    }

    logout() {
        this.router.navigate(['/login']);
        this.authService.doLogout()
            .then((result) => {
                console.log('Logout OK' + result);
            }, (error) => {
                console.log('Logout error', error);
            });
    }
}
