import { Component, OnInit } from '@angular/core';
import { AuthService } from '../core/auth.service';
import { Router } from '@angular/router';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { User } from '../core/user';
import { AngularFireDatabase, AngularFireObject } from '@angular/fire/database';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  public loginForm: FormGroup;
  public errorMessage = '';
  public user: User = new User;
  public userReference: AngularFireObject<any>;

  ngOnInit() {
  }

  constructor(
    private afDb: AngularFireDatabase,
    private authService: AuthService,
    private router: Router,
    private formBuilder: FormBuilder
  ) {
    this.createForm();
  }

  createForm() {
    this.loginForm = this.formBuilder.group({
      email: ['', Validators.required],
      password: ['', Validators.required]
    });
  }

  tryLogin(formValues) {
    this.authService.doLogin(formValues)
      .then(result => {
        console.log(result);
        this.retriveData();
      }, error => {
        console.log(error);
        this.errorMessage = '*' + error.message;
      });
  }

  retriveData() {
    const id = this.authService.getCurrentUserId();
    this.userReference = this.afDb.object('users/' + id);
    this.userReference.snapshotChanges().subscribe(action => {
      this.user = action.payload.val() as User;
      if (this.user.role === 'administrator' || this.user.role === 'consultant') {
        this.router.navigate(['/dashboard']);
      } else if (this.user.role === 'farmer') {
        this.router.navigate(['/farmer-dashboard']);
      }
    });
  }
}
