import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { AngularFireDatabase, AngularFireObject } from '@angular/fire/database';
import { ContentService } from '../core/content.service';
import { AuthService } from '../core/auth.service';
import { User } from '../core/user';

@Component({
  selector: 'app-user-profile',
  templateUrl: './user-profile.component.html',
  styleUrls: ['./user-profile.component.css']
})
export class UserProfileComponent implements OnInit {

  public regionsList = ['Heraklion Crete', 'Chania Crete', 'Rehtimnon Crete', 'Lasithi Crete'];
  public user: User = new User;
  public userID: string;
  public userReference: AngularFireObject<any>;

  constructor(
    private afDb: AngularFireDatabase,
    private contentService: ContentService,
    private authService: AuthService
  ) {
    this.initVars();
    this.retriveUserData();
  }

  ngOnInit() {
    this.resetForm();
    this.retriveUserData();
  }

  initVars() {
    this.user.uid = '';
    this.user.provider = '';
    this.user.firstName = '';
    this.user.lastName = '';
    this.user.email = '';
    this.user.photo = 'assets/img/farmer.png';
    this.user.role = '';
    this.user.about = '';
    this.user.city = '';
    this.user.region = '';
  }

  retriveUserData() {
    this.userID = this.authService.getCurrentUserId();
    this.userReference = this.afDb.object('users/' + this.userID);
    this.userReference.snapshotChanges().subscribe(action => {
      this.user = action.payload.val() as User;
      if (this.user.role === 'administrator') {
        this.user.photo = 'assets/img/administrator.png';
      } else {
        this.user.photo = 'assets/img/farmer.png';
      }
    });
  }

  resetForm(form?: NgForm) {
    if (form != null) {
      form.reset();
    }
  }

  onSubmit(form: NgForm) {
    if (form.value.uid !== null) {
      this.contentService.updateUser(form.value);
    } else {
      this.resetForm(form);
    }
  }
}
