import { Injectable } from '@angular/core';
import { AngularFireDatabase } from '@angular/fire/database';
import { AngularFireStorage } from '@angular/fire/storage';
import * as firebase from 'firebase/app';
import { Upload } from './upload';
import { finalize } from 'rxjs/operators';

@Injectable()
export class UploadService {

    private basePath: String = '/uploads';
    public percent: number;

    constructor(
        private afS: AngularFireStorage,
        private afDb: AngularFireDatabase
    ) {
        this.percent = 0;
    }

    pushUpload(upload: Upload) {
        this.percent = 0;
        const fileRef = this.afS.ref(`${this.basePath}/${upload.file.name}`);
        const task = this.afS.upload(`${this.basePath}/${upload.file.name}`, upload.file);
        const uploadPercent = task.percentageChanges();
        uploadPercent.subscribe(percent => {
            this.percent = Math.ceil(percent);
        });

        task.snapshotChanges().pipe(
            finalize(() => {
                fileRef.getDownloadURL().subscribe(url => {
                    upload.createdAt = Date.now();
                    upload.url = url;
                    upload.name = upload.file.name;
                    this.saveFileData(upload);
                });
            })
        ).subscribe();
    }

    private saveFileData(upload: Upload) {
        this.afDb.list(`${this.basePath}/`).push(upload);
    }

    public deleteUpload(upload: Upload) {
        this.deleteFileData(upload.$key)
            .then(() => {
                this.deleteFileStorage(upload.name);
            })
            .catch(error => console.log(error));
    }

    private deleteFileData(key: string) {
        return this.afDb.list(`${this.basePath}/`).remove(key);
    }

    private deleteFileStorage(name: string) {
        const storageRef = firebase.storage().ref();
        storageRef.child(`${this.basePath}/${name}`).delete();
    }
}
