import { Component, OnInit } from '@angular/core';
import { ContentService } from '../core/content.service';
import { Income } from '../core/income';

declare interface TableData {
  headerRow: string[];
}

@Component({
  selector: 'app-total-incomes',
  templateUrl: './total-incomes.component.html',
  styleUrls: ['./total-incomes.component.css']
})
export class TotalIncomesComponent implements OnInit {

  public tableData: TableData;
  public incomesList: Income[];

  constructor(
    private contentService: ContentService
  ) { }

  ngOnInit() {
    this.tableData = {
      headerRow: ['ID', 'Farmer ID', 'Title', 'Ammount', 'Date', 'Description']
    };
    this.contentService.getIncomesData().snapshotChanges().subscribe(item => {
      this.incomesList = [];
      item.forEach(element => {
        const y = element.payload.toJSON();
        y['iid'] = element.key;
        this.incomesList.push(y as Income);
      });
    });
  }
}
