import { Component, OnInit } from '@angular/core';
import { AngularFireDatabase } from '@angular/fire/database';
import { AngularFireObject } from '@angular/fire/database';
import { AuthService } from '../../core/auth.service';
import { User } from '../../core/user';

declare interface RouteInfo {
    path: string;
    title: string;
    icon: string;
    class: string;
}

export const ADMINISTRATOR_ROUTES: RouteInfo[] = [
    { path: '/dashboard', title: 'Dashboard', icon: 'business_chart-bar-32', class: '' },
    { path: '/user-profile', title: 'User Profile', icon: 'users_single-02', class: '' },
    { path: '/total-users', title: 'Total Users', icon: 'users_circle-08', class: '' },
    { path: '/total-incomes', title: 'Total Incomes', icon: 'business_money-coins', class: '' },
    { path: '/total-outcomes', title: 'Total Outcomes', icon: 'shopping_credit-card', class: '' },
    { path: '/total-production', title: 'Total Production', icon: 'business_chart-pie-36', class: '' },
    { path: '/total-farms', title: 'Total Farms', icon: 'design_image', class: '' }
];

export const FARMER_ROUTES: RouteInfo[] = [
    { path: '/farmer-dashboard', title: 'Dashboard', icon: 'business_chart-bar-32', class: '' },
    { path: '/user-profile', title: 'User Profile', icon: 'users_single-02', class: '' },
    { path: '/incomes', title: 'Incomes', icon: 'business_money-coins', class: '' },
    { path: '/outcomes', title: 'Outcomes', icon: 'shopping_credit-card', class: '' },
    { path: '/production', title: 'Production', icon: 'business_chart-pie-36', class: '' },
    { path: '/farms', title: 'Farms', icon: 'design_image', class: '' }
];

export const CONSULTANT_ROUTES: RouteInfo[] = [
    { path: '/dashboard', title: 'Dashboard', icon: 'business_chart-bar-32', class: '' },
    { path: '/user-profile', title: 'User Profile', icon: 'users_single-02', class: '' },
    { path: '/consulting', title: 'Consulting', icon: 'education_hat', class: '' }
];

@Component({
    selector: 'app-sidebar',
    templateUrl: './sidebar.component.html',
    styleUrls: ['./sidebar.component.css']
})
export class SidebarComponent implements OnInit {

    public menuItems: any[];
    private user: User = new User;
    private userReference: AngularFireObject<any>;

    constructor(
        private afDb: AngularFireDatabase,
        private authService: AuthService
    ) {
        this.retriveData();
    }

    ngOnInit() { }

    isMobileMenu() {
        if (window.innerWidth > 991) {
            return false;
        }
        return true;
    };

    retriveData() {
        const id = this.authService.getCurrentUserId();
        this.userReference = this.afDb.object('users/' + id);
        this.userReference.snapshotChanges().subscribe(action => {
            this.user = action.payload.val() as User;
            if (this.user.role === 'administrator') {
                this.menuItems = ADMINISTRATOR_ROUTES.filter(menuItem => menuItem);
            }
            if (this.user.role === 'farmer') {
                this.menuItems = FARMER_ROUTES.filter(menuItem => menuItem);
            }
            if (this.user.role === 'consultant') {
                this.menuItems = CONSULTANT_ROUTES.filter(menuItem => menuItem);
            }
        });
    }
}
