import { Routes } from '@angular/router';
import { AdminLayoutComponent } from './layouts/admin-layout/admin-layout.component';
import { FarmerLayoutComponent } from './layouts/farmer-layout/farmer-layout.component';
import { ConsultantLayoutComponent } from './layouts/consultant-layout/consultant-layout.component';
import { LoginComponent } from './login/login.component';
import { RegisterComponent } from './register/register.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { UserProfileComponent } from './user-profile/user-profile.component';
import { TotalIncomesComponent } from './total-incomes/total-incomes.component';
import { TotalOutcomesComponent } from './total-outcomes/total-outcomes.component';
import { TotalProductionComponent } from './total-production/total-production.component';
import { TotalFarmsComponent } from './total-farms/total-farms.component';
import { TermsOfUseComponent } from './terms-of-use/terms-of-use.component';
import { AuthGuard } from './core/auth.guard';

export const AppRoutes: Routes = [
  { path: '', redirectTo: 'login', pathMatch: 'full' },
  { path: 'login', component: LoginComponent },
  { path: 'register', component: RegisterComponent },
  { path: 'terms-of-use', component: TermsOfUseComponent },
  {
    path: '', component: AdminLayoutComponent, children: [
      { path: '', loadChildren: './layouts/admin-layout/admin-layout.module#AdminLayoutModule', canActivate: [AuthGuard] },
      { path: 'dashboard', component: DashboardComponent, canActivate: [AuthGuard] },
      { path: 'user-profile', component: UserProfileComponent, canActivate: [AuthGuard] },
      { path: 'total-incomes', component: TotalIncomesComponent, canActivate: [AuthGuard] },
      { path: 'total-outcomes', component: TotalOutcomesComponent, canActivate: [AuthGuard] },
      { path: 'total-production', component: TotalProductionComponent, canActivate: [AuthGuard] },
      { path: 'total-farms', component: TotalFarmsComponent, canActivate: [AuthGuard] }
    ]
  },
  {
    path: '', component: FarmerLayoutComponent, children: [
      { path: '', loadChildren: './layouts/farmer-layout/farmer-layout.module#FarmerLayoutModule', canActivate: [AuthGuard] },
      { path: 'user-profile', component: UserProfileComponent, canActivate: [AuthGuard] }
    ]
  },
  {
    path: '', component: ConsultantLayoutComponent, children: [
      { path: '', loadChildren: './layouts/consultant-layout/consultant-layout.module#ConsultantLayoutModule', canActivate: [AuthGuard] },
      { path: 'dashboard', component: DashboardComponent, canActivate: [AuthGuard] },
      { path: 'user-profile', component: UserProfileComponent, canActivate: [AuthGuard] }
    ]
  },
  { path: '**', redirectTo: 'login' }
];
