import { Component, OnInit } from '@angular/core';
import { ContentService } from '../core/content.service';
import { Outcome } from '../core/outcome';

declare interface TableData {
  headerRow: string[];
}

@Component({
  selector: 'app-total-outcomes',
  templateUrl: './total-outcomes.component.html',
  styleUrls: ['./total-outcomes.component.css']
})
export class TotalOutcomesComponent implements OnInit {

  public tableData: TableData;
  public outcomesList: Outcome[];

  constructor(
    private contentService: ContentService
  ) { }

  ngOnInit() {
    this.tableData = {
      headerRow: ['ID', 'Farmer ID', 'Title', 'Ammount', 'Date', 'Description']
    };
    this.contentService.getOutcomesData().snapshotChanges().subscribe(item => {
      this.outcomesList = [];
      item.forEach(element => {
        const y = element.payload.toJSON();
        y['oid'] = element.key;
        this.outcomesList.push(y as Outcome);
      });
    });
  }
}
